    SetPixel(x: number, y: number, color: number) {
        const index = (y * this.width + x) * 4;
        this.data[index] = color & 0xff;
        this.data[index + 1] = (color >> 8) & 0xff;
        this.data[index + 2] = (color >> 16) & 0xff;
        this.data[index + 3] = (color >> 24) & 0xff;
    }

